<template>
  <div>
    <Page>
      <b-form>
        <b-form-row>
          <b-form-group label="Data Início" class="col-md-auto">
            <b-select plain size="md" v-model="period" @change="changeTime">
              <b-form-select-option :value="''">Atual</b-form-select-option>
              <b-form-select-option :value="'7'">7 dias</b-form-select-option>
              <b-form-select-option :value="'15'">15 dias</b-form-select-option>
              <b-form-select-option :value="'30'">30 dias</b-form-select-option>
              <b-form-select-option :value="'60'">60 dias</b-form-select-option>
            </b-select>
          </b-form-group>
          <b-form-group label="Data Início" class="col-md-auto">
            <date-picker
              v-model="time1"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Data Final" class="col-md-auto">
            <date-picker
              v-model="time2"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>

          <b-form-group label="Agentes" class="col-md-auto">
            <b-select plain multiple v-model="agent" :options="agents" size="md" />
          </b-form-group>
        </b-form-row>
        <b-form-row>
          <ladda-btn
            :loading="loading"
            data-style="expand-left"
            class="btn btn-success"
            @click.native="getPerformance"
            >Visualizar</ladda-btn
          >
        </b-form-row>
      </b-form>
    </Page>
    <Page :hidden="table.length == 0">
      <Table
        :tableData="table"
        :filename="fileName"
        :header="header"
        :tablefooter="footer"
        :legend="legend"
      />
    </Page>
  </div>
</template>
<script>
import Page from "@/components/Page";
import Table from "@/components/Table";
import LaddaBtn from "@/vendor/libs/ladda/Ladda";
import "@/vendor/libs/vue2-datepicker/locale/pt-br";
import DatePicker from "vue2-datepicker";
import moment from "moment";
import _ from "lodash";
import axios from "axios";
export default {
  name: "perfomance",
  components: {
    Page,
    DatePicker,
    LaddaBtn,
    Table,
  },
  metaInfo: {
    title: "Relatório Desempenho de Agente",
  },
  data: () => ({
    time1: moment().hour(0).minute(0).second(0).toDate(),
    time2: moment().hour(23).minute(59).second(59).toDate(),
    period: "",
    agents: [],
    agent: [],
    entry: "",
    queueLog: "",
    table: [],
    loading: false,
    header: [],
    legend: [],
    fileName: "",
    totalPause: [],
    footer: [],
  }),
  mounted() {
    this.$store.commit("setTitle", "Relatório Desempenho de Agente");
    this.loadAgents();
  },
  methods: {
    updateHeader() {
      this.header = [];
      this.header.push("<h4>Relatório Desempenho de Agente</h4>");
      this.header.push(
        `Período ${moment(this.time1).format("DD/MM/YYYY")} à ${moment(this.time2).format(
          "DD/MM/YYYY"
        )} / Hora à ${moment(this.time1).format("HH:mm:ss")} até ${moment(
          this.time2
        ).format("HH:mm:ss")}`
      );
      this.header.push(" ");
      this.fileName = `Rel_agente_desempenho ${moment().format("DD_MM_YYYY_HH_mm_ss")}`;

      this.legend = [
        "<h5>Legenda:</h5>",
        "<b>TTC</b> - Tempo total de chamada;",
        "<b>TMC</b> - Tempo médio de chamada;",
        "<b>TME</b> - tempo médio de espera;",
        "<b>Enc User</b> - Finalizadas pelo cliente;",
        "<b>Enc Oper</b> - Finalizadas pelo operador;",
        "<b>Tempo Login</b> - Tempo total de login do operador;",
        "<b>Tempo Pausa</b> - Tempo total de pausas do operador;",
        `<b>Emitido em:</b> ${moment().format("DD/MM/YYYY HH:mm:ss")}`,
      ];
    },
    changeTime: function () {
      this.time1 =
        this.period === ""
          ? moment().hour(0).minute(0).second(0).toDate()
          : moment()
              .add(this.period * -1, "d")
              .hour(0)
              .minute(0)
              .second(0)
              .toDate();
      this.time2 = moment().hour(23).minute(59).second(59).toDate();
    },
    loadAgents: function () {
      axios
        .post(`${location.origin}/reportsbackend/agents_callcenter.php`)
        .then((data) => {
          this.agents = [
            {
              text: "Todos",
              value: "",
            },
          ];
          data.data.forEach((x) => {
            this.agents.push({
              text: x.name,
              value: x.id,
            });
          });
        })
        .catch((err) => console.log(err));
    },
    getPerformance() {
      this.loading = true;
      this.table = [];

      const params = new URLSearchParams();
      params.append("time1", moment(this.time1).format("YYYY-MM-DD HH:mm:ss"));
      params.append("time2", moment(this.time2).format("YYYY-MM-DD HH:mm:ss"));
      params.append("agent", this.agent.join(","));
      axios
        .post(`${location.origin}/reportsbackend/report_perfomance_queue_log.php`, params)
        .then((data) => {
          this.queueLog = data.data;
        })
        .catch((err) => console.log(err));

      axios
        .post(`${location.origin}/reportsbackend/report_total_pause_agents.php`, params)
        .then((data) => {
          this.totalPause = data.data;
        })
        .catch((err) => console.log(err));

      axios
        .post(
          `${location.origin}/reportsbackend/report_perfomance_call_entry.php`,
          params
        )
        .then((data) => {
          if (data.data.length === 0) {
            this.$bvToast.toast("Nenhum registro encontrado", {
              title: "Tecnomafer Report",
              autoHideDelay: 5000,
              appendToast: true,
              toastClass: "bs4-toast bg-danger",
            });
          }
          this.entry = data.data;
          this.mountTable();
          this.updateHeader();
        })
        .catch((err) => console.log(err))
        .finally((x) => {
          this.loading = false;
        });
    },
    formatTime(secTemp) {
      const sec = secTemp.toFixed(0);
      let hours = Math.floor(sec / 3600);
      let minutes = Math.floor((sec - hours * 3600) / 60);
      let seconds = sec - hours * 3600 - minutes * 60;
      if (hours < 10) {
        hours = "0" + hours;
      }
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      if (seconds < 10) {
        seconds = "0" + seconds;
      }
      return hours + ":" + minutes + ":" + seconds;
    },
    mountTable() {
      const tempTable = [];
      this.entry.forEach((x) => {
        const status = this.queueLog.find((y) => y.callid === x.id);
        const finalStatus = status ? status.event : "COMPLETEAGENT";

        tempTable.push({
          agent: x.Agente,
          id: x.id,
          duration: parseInt(x.Duration),
          durationWait: parseInt(x.DurationWait),
          durationPause: 0,
          status: finalStatus,
        });
      });

      this.totalPause.forEach((x) => {
        tempTable.push({
          agent: x.Agente,
          id: x.total + 100,
          duration: 0,
          durationWait: 0,
          durationPause: parseInt(x.total),
          status: "Pausa",
        });
      });

      this.table = _.chain(tempTable)
        .groupBy("agent")
        .map((array, key) => ({
          Agente: key,
          TTC: this.formatTime(_.sumBy(array, "duration")),
          TMC: this.formatTime(_.sumBy(array, "duration") / tempTable.length),
          TME: this.formatTime(_.sumBy(array, "durationWait") / tempTable.length),
          "Enc User": tempTable.reduce((counter, obj) => {
            if (obj.status === "COMPLETECALLER" && obj.agent === key) {
              counter += 1;
            }
            return counter;
          }, 0),
          "Enc Oper": tempTable.reduce((counter, obj) => {
            if (obj.status === "COMPLETEAGENT" && obj.agent === key) {
              counter += 1;
            }
            return counter;
          }, 0),
          "Tempo Login": this.formatTime(
            _.sumBy(array, "durationWait") +
              _.sumBy(array, "duration") +
              _.sumBy(array, "durationPause")
          ),
          "Tempo Pausa": this.formatTime(_.sumBy(array, "durationPause")),
        }))
        .value();

      let totalTTC = "00:00:00";
      let totalTMC = "00:00:00";
      let totalTME = "00:00:00";
      let encUse = 0;
      let encOper = 0;
      let totalLogin = "00:00:00";
      let totalPause = "00:00:00";

      this.table.forEach((x) => {
        totalTTC = this.sumTotalHour(totalTTC, String(Object.values(x)[1]));
        totalTMC = this.sumTotalHour(totalTMC, String(Object.values(x)[2]));
        totalTME = this.sumTotalHour(totalTME, String(Object.values(x)[3]));

        encUse += parseInt(Object.values(x)[4]);
        encOper += parseInt(Object.values(x)[5]);

        totalLogin = this.sumTotalHour(totalLogin, String(Object.values(x)[6]));
        totalPause = this.sumTotalHour(totalPause, String(Object.values(x)[7]));
      });
      this.footer = [];
      this.footer.push("Total");
      this.footer.push(totalTTC);
      this.footer.push(totalTMC);
      this.footer.push(totalTME);
      this.footer.push(encUse);
      this.footer.push(encOper);
      this.footer.push(totalLogin);
      this.footer.push(totalPause);
    },
    sumTotalHour(total, time) {
      const totalSplit = total.split(":");
      const timeSplit = time.split(":");

      const totalHour = parseInt(totalSplit[0]);
      const totalMinutes = parseInt(totalSplit[1]);
      const totalSeconds = parseInt(totalSplit[2]);

      const timeHour = parseInt(timeSplit[0]);
      const timeMinutes = parseInt(timeSplit[1]);
      const timeSeconds = parseInt(timeSplit[2]);

      const hours = (totalHour + timeHour) * 60 * 60;
      const minutes = (totalMinutes + timeMinutes) * 60;
      const seconds = totalSeconds + timeSeconds;
      return this.formatTime(hours + minutes + seconds);
    },
  },
};
</script>
